.split {
  height: 92.75vh;
  margin-top: 7.25vh;
  width: 50%;
  position: fixed;
  z-index: 1;
  top: 0;
  overflow-x: hidden;
}

/* Control the left side */
.left {
  background-color: rgba(0, 0, 0, 0.3);
  padding-left: 15%;
  padding-top: 10%;
  left: 0;
}

/* Control the right side */
.right {
  background-color: rgba(0, 0, 0, 0.5);
  padding-right: 8%;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  padding-top: 8%;
  right: 0;
}

